    isFieldValid(field: string) {
        return (
            (!this.form.get(field).valid && this.form.get(field).touched) ||
            (this.form.get(field).untouched && this.formSumitAttempt)
        );
    }

    displayFieldCss(field: string) {
        return {
            'has-error': this.isFieldValid(field),
            'has-feedback': this.isFieldValid(field)
        };
    }

    selected(value: any): string {
        value.id = this.items.indexOf(value.text);
        return value;
        /*console.log('Selected value is: ', value);*/

    }

    removed(value: any): void {
        console.log('Removed value is: ', value);
    }

    refreshValue(value: any): void {
        this.value = value;
    }






    onSubmit() {
        let input: any;
        this.formSumitAttempt = true;
        console.log(this.form.value);
        // if (this.form.valid) {
        input = { form_data: this.form.value };
        input.form_data.id = this.id;
        input.form_data.user_id = this.currentUser.success.id;
        if (this.uploaded_image == 1) {
            input.form_data.product_img = this.productImgUrl;
        }

        if (this.form.value.product_name != "" && this.form.value.availability) {
            this._productService.updateProduct(input).subscribe(
                data => {
                    this.product_id = data.success;
                    this.toastr.success('Product Update Successfully', 'Success Message');
                    this.reset();
                    this.router.navigate(['product']);
                },
            );
        }


        //  }
    }




    onSubmitSubCat() {
        let input: any;
        this.formSumitAttemptSubCat = true;

        this.route.params.subscribe(
            params => {
                if (params.product_id) {
                    this.product_id = params.product_id;
                    if (this.formSubCat.valid) {
                        input = { form_data: this.formSubCat.value };
                        input.form_data.fk_user_id = this.currentUser.success.id;
                        input.form_data.fk_product_id = this.product_id;
                        // console.log(input);
                        this._productService.createProductSubCat(input).subscribe(
                            data => {
                                this.reset();
                                this.toastr.success('Product Sub Category Added Successfully', 'Success Message');
                                this._productService.productSubCatList(params.product_id).subscribe(
                                    data => {
                                        this.subCategories = data.success;
                                    }
                                );
                            }
                        );
                    }
                }
            }
        );

    }

    onProductTypeSubmit() {

        let input: any;
        this.formSumitAttemptProductType = true;

        this.route.params.subscribe(
            params => {
                if (params.product_id) {
                    this.product_id = params.product_id;

                    if (this.formProductType.valid) {
                        input = { form_data: this.formProductType.value };
                        input.form_data.fk_user_id = this.currentUser.success.id;
                        input.form_data.fk_product_id = this.product_id;
                        //  console.log(input);
                        this._productService.createProductType(input).subscribe(
                            data => {
                                this.toastr.success('Product Types Added Successfully', 'Success Message');
                                this.reset();
                                this._productService.productTypeList(params.product_id).subscribe(
                                    data => {
                                        this.productTypeList = data.success;
                                    }
                                );

                            }
                        );
                    }
                }
            }
        );


    }

    onUploadSuccess(event) {
        /*console.log('event');
        console.log(event);
        console.log(event[0].dataURL);
        console.log(event[0].name);*/
        this.productImgUrl = event[0].dataURL;
        this.uploaded_image = 1;
    }

    onUploadError(event) {
        console.log('event');
        console.log(event);
        this.productImgUrl = event[0].dataURL;
        console.log('added');
        this.uploaded_image = 1;

    }


    onSubmitAddAttr() {
        let input: any;
        this.formSumitAttemptAttr = true;
        if (this.formAttr.valid) {
            input = { form_data: this.formAttr.value };
            input.form_data.fk_user_id = this.currentUser.success.id;
            input.form_data.fk_product_id = this.product_id;
            this._productService.addAttribute(input).subscribe(
                data => {
                    this.reset();
                }
            );
        }
    }


    onSubmitAss() {
        let input: any;
        this.formSumitAttemptAccess = true;
        this.route.params.subscribe(
            params => {
                if (params.product_id) {
                    this.toastr.success('Product Added Successfully', 'Success Message');
                    this.product_id = params.product_id;
                    if (this.formAccess.valid) {
                        input = { form_data: this.formAccess.value };
                        input.form_data.fk_user_id = this.currentUser.success.id;
                        input.form_data.fk_acc_product_id = this.product_id;
                        this._productService.addProductAccess(input).subscribe(
                            data => {
                                this.reset();
                                this._productService.productaccessList(params.product_id).subscribe(
                                    data => {
                                        this.accessData = data.success;
                                        if (this.accessData.length > 0) {
                                            this.accessData_bool = 1;
                                        }
                                        // console.log('Access Data', this.accessData);
                                    }
                                );
                            }
                        );
                    }
                }
            }
        );


    }


    public accessDelete(del_id) {
        console.log('accessDelete', del_id);


        let value = confirm('Are you want to delete this accessories ?');
        if (value) {

            this._productService.accessDelete(del_id).subscribe(
                data => {
                    this.accessData = data.success;
                    if (this.accessData.length > 0) {
                        this.accessData_bool = 1;
                    }
                }
            );
        }
    }




