    public changeProductTypeStatus(user: any) {
        if (user.status == 'Active') {
            user.status = 'Inactive';

            this._productService.updateProductTypeStatus(user.id).subscribe(
                data => {
                }
            );
        } else {
            user.status = 'Active';
            this._productService.updateProductTypeStatus(user.id).subscribe(
                data => {
                }
            );
        }
    }




    handleFileInput(files: FileList) {
        let file = files.item(0);
        var reader = new FileReader();
        reader.onload = (event: any) => {
            this.productImgUrl = event.target.result;
        }
        reader.readAsDataURL(file);
    }

    multipleHandleFileInput(files: FileList) {
        let filess = [];
        for (var i = 0; i < files.length; ++i) {
            filess.push(files.item(i));
        }
        filess.forEach(element => {
            var reader = new FileReader();
            reader.onload = (event: any) => {
                this.alternateProductImgUrl.push(event.target.result);
            }
            reader.readAsDataURL(element);
        });
    }
    multipleUpload() {
        this._productService.multipleUpload({ img: this.alternateProductImgUrl, fk_product_id: parseInt(this.product_id) }).subscribe(
            data => {

            }
        );
    }

    removeAttribute(attribute_id) {
        let temp = [];
        this.custom_attribite_list.forEach(data => {
            if (data.id == attribute_id) {
                console.log(attribute_id);
                console.log('reached');
            } else {
                temp.push(data);
            }
        });
        this.custom_attribite_list = temp;
        if (this.custom_attribite_list.length) {
            this._productService.addAttributeList({ "product_id": this.product_id, "form_data": this.custom_attribite_list }).subscribe(data => {
                //console.log(data);
                this.toastr.success('You have successfully updated product attributes.', 'Success Message');
            });
        }
    }

    addAttribute() {
        let counter = 1;
        // let selecteddata = [];
        if (this.attributes != '') {
            if (this.custom_attribite_list == null) {
                this.custom_attribite_list = [];
            } else {
                this.custom_attribite_list.forEach(data => {
                    if (data.id == this.attributes) {
                        counter = 0;
                    }
                })
            }
            if (counter == 1) {
                this._productService.getAttributeById(parseInt(this.attributes), parseInt(this.product_id)).subscribe(data => {

                    let testObj = { "id": data.success.id, "name": data.success.name, "data": JSON.parse(data.success.data) };

                    this.custom_attribite_list.push(testObj);

                });


            } else {
                console.log('already exists');
            }
        }
    }

    saveAttribute() {
        if (this.custom_attribite_list.length) {
            this._productService.addAttributeList({ "product_id": this.product_id, "form_data": this.custom_attribite_list }).subscribe(data => {
                console.log(data);
                this.toastr.success('You have successfully updated product attributes.', 'Success Message');
            });
        }
    }

    saveAccesories() {
        if (this.selectedProduct != '') {

            let input = { form_data: { 'fk_category': this.selectedCategories, 'fk_sub_cat': '', 'accessory_product_id': this.selectedProduct, 'fk_acc_product_id': this.product_id, 'fk_user_id': this.currentUser.success.id } };
            this._productService.addProductAccess(input).subscribe(
                data => {
                    //this.reset();
                    this._productService.productaccessList(this.product_id).subscribe(
                        data => {
                            this.accessData = data.success;
                            if (this.accessData.length > 0) {
                                this.accessData_bool = 1;
                                this.toastr.success('You have successfully selected product.', 'Success Message');
                            }
                            console.log('Access Data', this.accessData);
                        }
                    );
                }
            );
        } else {
            this.toastr.error('You have not selected product.', 'Error Message');
        }
    }
